.Weather {
    background-color: rgba(255,255,255,.7);
    border-radius: 10px;
    box-shadow: 0px 0px 13px 6px rgba(133,133,133,0.28);
    max-width: 800px;
    min-height: 391px;
    min-width: 800px;
}

.weatherInfo {
    padding: 15px;
    display: flex;
    align-items: flex-start;
    flex-direction: row;
    min-width: 530px;
    flex-wrap: wrap;
    transition: height 1s ease;
}

.errorInfo {
    height: 391px;
    display: grid;
    justify-content: center;
    align-items: center;
}

.weatherHeader {
    width: 100%;
    flex-grow: 1;
    flex-basis: calc(100% / 3);
}

.weatherHeader div {
    display: flex;
    align-items: center;
}

.weatherHeader div:nth-child(n+2) {
    padding-left: 10px;
}

.weatherHeader div p:nth-child(1) {
    font-weight: 300;
}

.weatherHeader span {
    height: 5px;
    width: 5px;
    margin: 0px 8px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
}

.weatherHeader img {
    vertical-align: middle;
}

.weatherHeader h1 {
    padding-left: 10px;
    color: #b07acd;
    vertical-align:middle;
}


.temperature {
    width: 100%;
    text-align: right;
    flex-grow: 1;
    flex-basis: calc(100% / 3);
}

.temp {
    font-size: 100px;
    font-weight: 600;
    color: #48484A;
}

.wind {
    margin-top: 20px;
    padding-bottom: 5px;
}

.wind p, .humid p {
    padding-left: 10px;
    text-transform: lowercase;
}

.dailyForecast {
    flex-grow: 1;
    flex-basis: calc(100% / 3);
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(5, 1fr);
    margin-top: 40px;
    grid-gap: 10px;
    text-align: center;
}

.dailyForecast div {
    background: rgba(136, 0, 226, 0.06);
    padding: 12px;
    border-radius: 10px;
}

.dailyForecast h3 {
    color: #b07acd;
    margin: 0;
}

.dailyForecast p {
    font-weight: 300;
    font-size: 14px;
}

.dailyForecast p:nth-child(4) {
    font-size: 20px;
    font-weight: 600;
    color: #48484A;
}


p {
    margin: 0;
    text-transform: capitalize;
    color: #767676;
}