.Form {
    justify-self: right;
}


.Form input {
    outline: 0;
    background: #f2f2f2;
    border: 0;
    margin: 0 0 15px;
    padding: 15px;
    box-sizing: border-box;
    font-size: 14px;
    border-radius: 10px 0px 0px 10px;
}

.Form button {
    text-transform: uppercase;
    outline: 0;
    background: #c794c1;
    border: 0;
    padding: 15px;
    color: #FFFFFF;
    font-size: 14px;
    -webkit-transition: all 0.3 ease;
    transition: all 0.3 ease;
    cursor: pointer;
    border-radius: 0px 10px 10px 0px;
}