@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'Montserrat', 'sans-serif';
}


.App {
  min-height: 100vh;
  background: lightblue;
  background-image: url(/weatherapp/static/media/background.6f381937.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

}


.header {
    display: grid;
    grid-template-columns: 1fr 2fr;
    align-items: center;
}

main {
  display:grid;
  justify-content: center;
  align-items: center;
  padding: 25px; 
}

header {
  padding: 25px;
}

h1 {
  margin: 0;
}



.Weather {
    background-color: rgba(255,255,255,.7);
    border-radius: 10px;
    box-shadow: 0px 0px 13px 6px rgba(133,133,133,0.28);
    max-width: 800px;
    min-height: 391px;
    min-width: 800px;
}

.weatherInfo {
    padding: 15px;
    display: flex;
    align-items: flex-start;
    flex-direction: row;
    min-width: 530px;
    flex-wrap: wrap;
    -webkit-transition: height 1s ease;
    transition: height 1s ease;
}

.errorInfo {
    height: 391px;
    display: grid;
    justify-content: center;
    align-items: center;
}

.weatherHeader {
    width: 100%;
    flex-grow: 1;
    flex-basis: calc(100% / 3);
}

.weatherHeader div {
    display: flex;
    align-items: center;
}

.weatherHeader div:nth-child(n+2) {
    padding-left: 10px;
}

.weatherHeader div p:nth-child(1) {
    font-weight: 300;
}

.weatherHeader span {
    height: 5px;
    width: 5px;
    margin: 0px 8px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
}

.weatherHeader img {
    vertical-align: middle;
}

.weatherHeader h1 {
    padding-left: 10px;
    color: #b07acd;
    vertical-align:middle;
}


.temperature {
    width: 100%;
    text-align: right;
    flex-grow: 1;
    flex-basis: calc(100% / 3);
}

.temp {
    font-size: 100px;
    font-weight: 600;
    color: #48484A;
}

.wind {
    margin-top: 20px;
    padding-bottom: 5px;
}

.wind p, .humid p {
    padding-left: 10px;
    text-transform: lowercase;
}

.dailyForecast {
    flex-grow: 1;
    flex-basis: calc(100% / 3);
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(5, 1fr);
    margin-top: 40px;
    grid-gap: 10px;
    text-align: center;
}

.dailyForecast div {
    background: rgba(136, 0, 226, 0.06);
    padding: 12px;
    border-radius: 10px;
}

.dailyForecast h3 {
    color: #b07acd;
    margin: 0;
}

.dailyForecast p {
    font-weight: 300;
    font-size: 14px;
}

.dailyForecast p:nth-child(4) {
    font-size: 20px;
    font-weight: 600;
    color: #48484A;
}


p {
    margin: 0;
    text-transform: capitalize;
    color: #767676;
}
.Form {
    justify-self: right;
}


.Form input {
    outline: 0;
    background: #f2f2f2;
    border: 0;
    margin: 0 0 15px;
    padding: 15px;
    box-sizing: border-box;
    font-size: 14px;
    border-radius: 10px 0px 0px 10px;
}

.Form button {
    text-transform: uppercase;
    outline: 0;
    background: #c794c1;
    border: 0;
    padding: 15px;
    color: #FFFFFF;
    font-size: 14px;
    -webkit-transition: all 0.3 ease;
    transition: all 0.3 ease;
    cursor: pointer;
    border-radius: 0px 10px 10px 0px;
}
.Navbar ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.Navbar li {
    display: inline;
    padding-right: 10px; 
}

li a {
    text-decoration: none;
    color: white;
}
.About .container {
    background-color: rgba(255,255,255,.7);
    border-radius: 10px;
    box-shadow: 0px 0px 13px 6px rgba(133,133,133,0.28);
    max-width: 800px;
    min-width: 800px;
    min-height: 391px;
   
}

.container h1 {
    margin-bottom: 20px;
    color: #b07acd;
}


.container div {
    padding: 15px;
}
